body {
  max-width: 768px;
  margin: 0 auto;
}

:active, :hover, :focus {
  outline: 0;
  outline-offset: 0;
}

.isHidden {
  display: none; 
}

main {
  margin-top: 110px;
  margin-bottom: 70px;
}

ul {
  list-style: none;
  padding: 0;
  display: flex;
  flex-direction: column-reverse;
}

ul:empty:after {
  padding: 30px 10px;
  content: 'Nothing there, but you can add something above.';
  font-size: 32px;
  text-align: center;
}

ul li[value="#62bfbf"] {
  background-color: #62bfbf;
}

ul li[value="#2196F3"] {
  background-color: #2196F3;
}

.sideWrapper {
    position: fixed;
    width: 100vw;
    height: 100vh;
    z-index: 1;
}

.sideBar {
    position: fixed;
    background-color: #2196f3;
    height: calc(100vh - 110px);
    width: 135px;
    padding: 0px 20px;
    font-size: 23px;
    z-index: 2;
}

.sideBar ul {
    flex-direction: column;
}

.sideBar ul li:hover {
    cursor: pointer;
    color: antiquewhite;
}

li {
  border-bottom: aliceblue 1px solid;
  max-width: 768px;
  padding: 2px 0;
  display: flex;
  justify-content: space-between;
}

.taskBox {
  flex-basis: 80%;
  margin-left: 38px;
}

.taskColorMark {
  width: 56px;
  height: 6px;
  display: inline-block;
  font-size: 12px;
  color: #ffffff;
  padding: 0 0 9px 2px;
}

.creationDate {
  font-size: 9px;
  margin-left: 3px;
} 

.active .creationDate {
  color: lightslategrey;
}

.archive .creationDate {
  border: 1px solid lightgrey;
  background-color: #ccc;
  color: darkslategray;
}

.isFavorite {
  background: #f1da36; /* Old browsers */ /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg, #f1da36 2%,#fefcea 52%); /* Chrome10-25,Safari5.1-6 */
  background: -webkit-linear-gradient(315deg, #f1da36 2%,#fefcea 52%);
  background: linear-gradient(135deg, #f1da36 2%,#fefcea 52%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#f1da36', endColorstr='#fefcea',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}

.favorite {
  visibility: hidden;
  cursor: pointer;
}

.favorite:before {
  content: "\2606";
  position: absolute;
  font-size: 19px;
  visibility: visible;
  line-height: 0.7;
  color: darkslategray;
}

.favorite:checked:before {
  content: "\2605";
  position: absolute;
  color: orange;
}

.taskTitle {
  text-indent: 9px;
  overflow-wrap: break-word;
  overflow: hidden;
  display: block;
}

.active .taskTitle {
  color: #5656ea;
}

.archive .taskTitle {
  text-decoration: line-through;
  color: gray;
}

.taskControl {
  display: flex;
  flex-basis: 5%;
  flex-wrap: wrap;
  margin-right: 7%;
  flex-direction: row-reverse;
  align-content: baseline;
}

.taskBtn {
  height: 22px;
  background: transparent;
  border: 1px solid deepskyblue;
  margin: 1px auto;
  width: 22px;
}

.taskBtn:hover {
  cursor: pointer;
  background-color: deepskyblue;
  color: white;
}

.checkContainer {
  display: block;
  position: relative;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 22px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.checkContainer input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}

.checkmark {
  flex-basis: 12%;
  position: absolute;
  top: 0;
  left: 0;
  height: 25px;
  width: 25px;
  margin-left: 10px;
  background-color: #eee;
}

.checkContainer:hover input ~ .checkmark {
  background-color: #ccc;
}

.checkContainer input:checked ~ .checkmark {
  background-color: #2196F3;
}

.checkmark:after {
  left: 9px;
  top: 5px;
  width: 5px;
  height: 10px;
  border: solid white;
  border-width: 0 3px 3px 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  content: "";
  position: absolute;
  display: none;
}

.checkContainer input:checked ~ .checkmark:after,
.checkContainer:hover input ~ .checkmark:after {
  display: block;
}


footer {
    position: fixed;
    bottom: 0;
    background: white;
    width: 100%;
    max-width: 768px;
    height: 60px;
    z-index: 1;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    border-top: 1px solid darkslateblue;
    padding-top: 3px;
}
.control,
.viewed {
    float: right;
    padding: 5px;
    display: flex;
    flex-wrap: nowrap;
}

footer button {
    background: transparent;
    border: 1px solid cornflowerblue;
    margin: 0 2px;
    height: 100%;
}

footer button:hover {
    background-color: cornflowerblue;
    color: white;
    cursor: pointer;
}

@media only screen 
    and (min-device-width: 400px)  {
        footer input[type="radio"],
        footer label {
            display: none;
        }
    }
/* Portrait */
@media only screen 
  and (max-device-width: 400px) {
    .control,
    .viewed {
        display: none;
        width: 80%;
    }
    
    .showControl:checked ~ .control {
        display: flex;
    }
    
    .showViewed:checked ~ .viewed {
        display: flex;
    }
    
    input[type="radio"] {
        position: absolute;
        z-index: -1;
        opacity: 0;
        margin: 10px 0 0 7px;
      }
    
    input[type="radio"] + label {
        padding: 5px 5px 5px 30px;
        cursor: pointer;
        height: 22px;
        width: 60px;
    }
    
    label[for="showControl"] {
        align-self: flex-end;
        position: absolute;
    }
    
    input[type="radio"] + label:before {
        content: '';
        position: absolute;
        top: 4px;
        left: 2px;
        width: 22px;
        height: 22px;
        border: 1px solid #CDD1DA;
        border-radius: 50%;
        background: #FFF;
    }
    
      input[type="radio"] + label:after {
        content: '';
        position: absolute;
        top: 8px;
        left: 6px;
        width: 16px;
        height: 16px;
        border-radius: 50%;
        background: #62bfbf;
        box-shadow: inset 0 1px 1px rgba(0,0,0,.5);
        opacity: 0;
        -webkit-transition: .2s;
        transition: .2s;
    }
    
    input[type="radio"]:checked + label:after {
        opacity: 1;
    }
    
    footer button {
        padding: 0;
        flex-basis: 100%;
    }
}

header {
  position: fixed;
  width: 100%;
  top: 0;
  background-color: white;
  height: 110px;
  z-index: 1;
}

.logo {
  width: 50px;
  float: left;
  margin: 15px;
}

.inputGroup {
  position: relative;
  font-size: 20px;
  margin-bottom: 5px;
  max-width: 768px;
}

.taskInput {
  width: 90%;
  margin-left: 1%;
  border: none;
  border-bottom: 1px slateblue solid;
  padding-left: 5px;
}

.taskInputBtn {
  position: absolute;
  bottom: 0;
  right: 0;
  border: none;
  padding-left: 20px;
  margin-right: 8%;
  font-size: 16px;
  background-color: transparent;
  color: gainsboro;
}

.taskColorPicker {
  position: absolute;
  bottom: 0;
  right: 50px;
  border: none;
  margin-right: 8%;
  font-size: 16px;
  background-color: transparent;
  color: gainsboro;
}

.taskInput:not(:placeholder-shown) ~ .taskInputBtn,
.taskInput:not(:placeholder-shown) ~ .taskColorPicker,
.taskInput:focus ~ .taskColorPicker,
.taskInput:focus ~ .taskInputBtn {
  bottom: 20px;
}

.taskInputBtn:hover {
  cursor: pointer;
}

.menuBar{
  width: 35px;
  height: 30px;
  margin: 10px 10px;
  position: relative;
  cursor: pointer;
  float: left;
}

@media only screen 
  and (max-device-width: 736px)
  and (orientation: landscape) {
    header {
      height: 85px;
    }
    main {
      margin-top: 85px;
    }
    .logo {
      width: 29px;
    }
    h1 {
      font-size: 24px;
    }
  }
